.moviesCard {
  display: flex;
  flex-direction: column;
  background-color: #222222;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.08);
}

.moviesCard__title-wrapper {
  padding: 14px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.moviesCard__title {
  margin: 0 0 10px;
  font-size: 15px;
  font-weight: 500;
}

.moviesCard__duration {
  margin: 0;
  font-size: 11px;
  color: #8B8B8B;
}

.moviesCard__btn {
  width: 30px;
  height: 30px;
}

