.promo {
  min-height: 524px;
  display: flex;
  justify-content: center;
  background: #073042 url("../../../images/pic__COLOR_landing-logo.svg") no-repeat center 72px;
  background-size: 320px 320px;
}

.promo__title {
  margin: 0;
  padding: 184px 0 0;
  max-width: 730px;
  font-size: 50px;
  font-weight: 400;
  line-height: 1.16;
  text-align: center;
  letter-spacing: -0.04em;
}

@media (max-width: 1024px) {
  .promo {
    min-height: 784px;
    background-position: center 224px;
  }

  .promo__title {
    padding-top: 326px;
  }
}

@media (max-width: 576px) {
  .promo {
    padding: 0 10px;
    min-height: 570px;
    background-position: center 142px;
    background-size: 248px 248px;
  }

  .promo__title {
    padding-top: 207px;
    font-size: 31px;
    line-height: 1.25;
  }
}
